.navigation {
  color: rgb(236, 170, 104);
  background-color: rgba(0, 0, 0, 0.495);
  padding: 10px;
  width: 100%;
  font-size: 1.4em;
  @media screen and (max-width: 740px) {
    padding: 0;
  }
  #nav {
    display: flex;
    justify-content: space-around;
  }
  a {
    color: rgb(245, 245, 245);
  }
  .nav-active {
    color: rgb(248, 146, 44);
  }
  ul:hover li:not(:hover) {
    opacity: 1;
  }
  ul:hover li:hover {
    color: rgb(255, 254, 252);
  }

  .nav-portfolio {
    cursor: pointer;
    display: flex;
    transition: 0.5s;

    &:hover .nav-projects,
    .nav-projects:hover {
      visibility: visible;
      opacity: 1;

      li {
        transform: rotatey(0deg);
      }
    }

    .nav-projects {
      position: absolute;
      left: 33%;
      top: 6%;
      visibility: hidden;
      opacity: 0;
      padding: 0 40px;
      width: 300px;
      transform: translateX(75px);
      transition: 0.3s;
      perspective: 500px;
      @media screen and(max-width: 450px) {
        left: 10%;
      }
      @media screen and(min-width: 1300px) {
        left: 38%;
      }
      @media screen and(min-width: 2300px) {
        left: 43%;
        top: 4%;
      }

      li {
        transform: rotatey(30deg) scaleX(1.2);
        transform-origin: center;
        color: rgb(248, 146, 44);

        transition: 0.3s;
      }
    }
  }
  #nav {
    @media screen and(max-width: 740px) {
      display: none;
    }
  }

  @media screen and(max-width: 740px) {
  }
}
#mobile-nav {
  display: none;
  user-select: none;
  @media screen and(max-width: 740px) {
    display: flex;
    height: 100%;
    position: fixed;
    background-color: rgba(245, 222, 179, 0.745);
    width: 60%;
    z-index: 10;
    justify-content: center;
    align-items: center;
    #nav-roll {
      a {
        color: black;
      }
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      #active-mobile-nav {
        display: none;
        font-size: 0.3em;
      }
    }
    span {
      cursor: pointer;
      font-size: 2em;
      @media screen and(max-width: 480px) {
        font-size: 1.5em;
      }
    }
    ul {
      height: 100%;
      text-align-last: left;
      margin: 0;
    }
    li {
      font-size: 2em;
      margin: 15px 0;
      cursor: pointer;
      transition: 0.2s ease-in-out;
      @media screen and(max-width: 480px) {
        font-size: 1.5em;
      }
      &:hover {
        color: whitesmoke;
      }
    }
  }
}
#burger {
  display: none;
}
@media screen and (max-width: 740px) {
  #burger {
    display: block;
    cursor: pointer;
    position: fixed;
    left: 5%;
    top: 0;
    font-size: 3em;
    z-index: 1000;
    color: black;
    transition: 0.3s ease-in-out;
    &:hover {
      color: whitesmoke;
      transform: scale(1.1);
    }
  }
}
