main {
  height: 120vh;
  overflow: hidden;
  background: url("../../../public/images/bg.jpg") center/cover;

  .contact {
    color: whitesmoke;
    @media screen and (max-width: 740px) {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
    }

    @keyframes contactAnim {
      50% {
        transform: scale(1.4);
      }
      100% {
        transform: scale(1);
      }
    }
  }

  .logo {
    top: 8px;
    right: 1rem;
  }

  .form-container {
    margin: 7% 0 0 24%;
    max-width: 50%;
    position: relative;
    @media screen and (min-width: 741px) {
      max-width: 300px;
    }
    @media screen and (max-width: 740px) {
      width: 100%;
    }

    h2 {
      text-align: center;
      margin-bottom: 50px;
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      @media screen and(max-width: 740px) {
        margin-top: 60px;
      }
    }

    .form-content {
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      display: grid;
      @media screen and (max-width: 740px) {
        display: none;
      }
      input,
      textarea {
        background: none;
        font-size: 1.2rem;
        border: 1px solid rgba(204, 122, 55, 0.822);
        color: whitesmoke;
        border-radius: 5px;
        @media screen and (min-width: 1200px) {
          font-size: 1.5rem;
        }
      }
      input {
        padding: 10px 6px;
        margin-bottom: 30px;
      }
    }
    textarea {
      resize: none;
      padding: 6px 0;
      height: 70px;
      color: whitesmoke;
      @media screen and (max-width: 540px) {
        width: 75%;
        padding: 12px 0;
      }
    }
    input.button {
      background: rgba(220, 127, 70, 0.822);
      color: whitesmoke;
      height: 55px;
      width: 126px;
      border-radius: 50px;
      margin-top: 20px;
      font-size: 1.2rem;
      cursor: pointer;
      transition: 0.2s;
      border: none;
      &:hover {
        transform: scale(1.05);
      }
    }
  }
  .formMessage {
    p {
      border-radius: 20px;
      text-align: center;
      padding: 5px 0;
    }
    .success {
      background: rgb(139, 248, 139);
    }
    .error {
      background: rgb(246, 147, 147);
    }
  }

  .contact-infos {
    opacity: 0;
    filter: blur(4px);
    animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
    position: absolute;
    top: 30%;
    right: 0;
    display: grid;
    gap: 40px;
    grid-template-rows: repeat(4, 23%) 12%;
    height: 50%;
    width: 40%;
    @media screen and(min-width: 741px) {
      max-width: 500px;
    }

    @media screen and (max-width: 740px) {
      margin-top: 50px;
      display: flex;
      width: 100%;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      text-align: center;
    }

    h4 {
      font-size: 1.8rem;
      font-weight: 200;
      margin-bottom: 10px;
    }

    .address .content p,
    .email .content p,
    .phone .content p {
      transition: 0.25s;
      font-size: 1.2em;
      cursor: pointer;
      @media screen and (max-width: 740px) {
        font-size: 0.8em;
      }
      &:hover {
        transform: translateX(5px);
      }
    }

    .address {
      position: relative;
      text-align: center;
      margin: 20px;
    }
    .phone {
      position: relative;
      margin: 20px;

      .content {
        position: absolute;
        left: 40%;
        transform: translateX(-50%);
      }
    }
    .email {
      margin: 40px;
    }
    .social-network {
      position: fixed;
      width: 80%;
      bottom: 0;
      @media screen and (max-width: 540px) {
        width: 50%;
      }
    }
    .credits {
      position: fixed;
      bottom: 2%;
      left: 2%;
      text-align: center;
      margin: 0;
      padding: 0;
      transform: translateY(-50%);

      p {
        font-size: 0.9rem;
      }
    }
  }
  #resto {
    color: black;
  }
}
@keyframes animate {
  100% {
    transform: scale(0.9);
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
