.menu-container {
  min-height: 100vh;
  .social-network {
    bottom: 0;
    position: fixed;
    @media screen and (max-width: 740px) {
      width: 20%;
    }
  }
  margin-top: 0;
  @media screen and(max-width: 740px) {
    position: absolute;
  }
  overflow: hidden;
  padding-top: 0;
  background: url("../../../public/images/tarte-fraises.jpg") center/cover fixed;
  .logo {
    height: 90px;
    width: 90px;
    position: fixed;
  }
  .menu {
    text-align: center;
    animation: animate 2s forwards cubic-bezier(0.5, 1, 0.89, 1);
    h1 {
      text-transform: uppercase;
      font-style: italic;
      margin-top: 90px;
      color: whitesmoke;
      font-size: 3em;
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      @media screen and(max-width: 740px) {
        margin-top: 200px;
        font-size: 2.2em;
      }
    }
    h2 {
      font-style: italic;
      width: 50%;
      margin: 10px auto;
      color: whitesmoke;
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      @media screen and(max-width: 525px) {
        font-size: 1.8em;
      }
    }
    .grid-container {
      margin: 0 auto;
      width: 50%;
      margin-top: 40px;
      display: grid;
      grid-template-rows: 1fr 1fr 1fr;
      color: whitesmoke;
      border: 1px solid whitesmoke;
      @media screen and (max-width: 740px) {
        width: 90%;
      }
      ul {
        border: 1px solid whitesmoke;
        padding: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 2em;
        @media screen and(max-width: 1080px) {
          font-size: 1.2em;
        }
        @media screen and(max-width: 800px) {
          font-size: 0.9em;
        }
        .choice {
          flex: 9;
          border-right: 1px solid whitesmoke;
          opacity: 0;
          filter: blur(4px);
          text-align: left;
          animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
        }
        .price {
          opacity: 0;
          filter: blur(4px);
          animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
          flex: 1;
        }
      }
    }
    .description {
      color: whitesmoke;
      margin-top: 60px;
      font-size: 1.2em;
      p {
        margin-top: 40px;
        width: 50%;
        margin: 10px auto;
        font-size: 1.1em;
        opacity: 0;
        filter: blur(4px);
        animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
        @media screen and(max-width: 740px) {
          font-size: 0.8em;
        }
      }
    }
  }
  .scroll-bottom {
    @media screen and (max-width: 740px) {
      display: none;
    }
  }
  .facebook {
    background: url("../../../public/images/fb.png") center/cover;
    min-width: 300px;
    width: 30%;
    margin: 30px auto 0 auto;
    height: 110px;
    border-radius: 10px;
    opacity: 0;
    filter: blur(4px);
    animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
    -webkit-box-shadow: 2px 1px 15px 7px #00000083;
    box-shadow: 2px 1px 15px 7px #0000007f;
    @media screen and(max-width: 740px) {
      display: none;
    }
    a {
      color: whitesmoke;
    }
  }
}
@keyframes animate {
  100% {
    transform: scale(0.9);
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
@keyframes scroller {
  0% {
    font-size: 4em;
  }
  100% {
    font-size: 5em;
  }
}
